.App {
  text-align: center;
  max-width: 430px;
  padding: 5px;
  -webkit-backdrop-filter: blur(7px) saturate(200%);
  backdrop-filter: blur(7px) saturate(200%);
  background-color: rgba(78, 81, 87, 0.5);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.125);
  -webkit-box-shadow: 10px 5px 15px black;
          box-shadow: 10px 5px 15px black;
}

.wrapper {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(7, 1fr);
  gap: 5px;
  max-width: 415px;
}

.Display {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  overflow: hidden;
  color: orange;
  text-align: right;
  font-size: x-large;
  grid-row: 1 / 3;
  border: 3px solid lightgray;
  border-radius: 5px;
  padding: 50px;
  background-color: black;
}

.displayFormulaStr {
  padding: 5px;
}

.displayFormulaArr {
  color: transparent;
  max-width: 2px;
  max-height: 1px;
}

.displayTotal {
  padding: 5px;
  color: white;
  font-size: xx-large;
}

.Input {
  grid-row: 3 / 8;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 5px;
}

button {
  color: white;
  font-family: inherit;
  font-size: x-large;
  place-self: center;
  width: 100px;
  height: 100px;
  background-color: transparent;
  border: 1px solid lightgray;
  border-radius: 5px;
}

button:hover {
  color: black;
  background-color: rgba(255, 255, 255, 0.5);
}

.btn-dw {
  width: 205px;
}
.btn-dh {
  height: 205px;
}

@media only screen and (max-width: 450px) {
  .App {
    max-width: 350px;
  }
  .wrapper {
    max-width: 320px;
  }
  .Display {
    padding: 20px;
  }
  button {
    width: 60px;
    height: 60px;
  }
  .btn-dh {
    height: 125px;
  }
  .btn-dw {
    width: 125px;
  }
}



